.bs-advanced-blurb {
	a {
		&:hover {
			text-decoration: none !important;
		}
	}

	.video-play-icon {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		height: auto;
		margin: auto;
	}
}

.fancybox-container {
	.fancybox-slide {
		.fancybox-content {
			width: 80%;
			height: 80%;
		}
	}

	.fancybox-content--social {
		a:hover {
			img {
				opacity: 0.7;
			}
		}
	}
}
