$DEFAULT__bs-section-image-wrapper--height: 100%;
 $DEFAULT__bs-section-image-wrapper--width: 100%;
 $DEFAULT__bs-section-image--height: 100%;
 $DEFAULT__bs-section-image--width: 100%;

    

 
.bs-section {
  $this: &;
  &.bs-section---default {
        #{$this}__image-container {
            position: absolute;
            height: $DEFAULT__bs-section-image-wrapper--height;
            width: $DEFAULT__bs-section-image-wrapper--width;
            z-index: -1;
            img {
                height: $DEFAULT__bs-section-image--height;
                width: $DEFAULT__bs-section-image--width;
                object-fit: cover;
            }
        }
  }

  position: relative;
  background-repeat: no-repeat;
  background-size: cover;

  &__video {
	margin: auto;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;

	.video-poster,
	video {
	  position: absolute;
	  width: 100%;
	  height: 100%;
	  object-fit: cover;
	}

	.video-poster {
	  img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	  }
	}
  }
}

.bs-background {
  img {
	width: 100%;
	height: 100%;
  }
}
