@import "../../../theme-defaults.scss";
@import "./grid-styles";

/* Logo item styles */
.bs-logo-slider {
  &__inner {
    display: flex;
    flex-wrap: wrap;

    &--variable-width {
      justify-content: space-between;

      .bs-logo-slider__image {
        height: 100px;
        object-fit: scale-down;
      }
    }
  }
  &__item {
    display: flex;
    justify-content: center;
    flex-direction: column;

    &__inner {
      margin: 0 auto;
    }
  }
  &__image {
    display: block;
    max-width: 100%;
    margin: 0 auto;
  }
  &__caption {
    margin-top: 10px;
    text-align: center;
  }
}
