@import "../../../theme-defaults.scss";
$bs-slider-accordion: ".bs-slider-accordion";

#{$bs-slider-accordion} {
  & :not(.bs-slider-accordion-slick)[id^="accordion_"] {
    display: flex;
    & .card {
      flex: 1;
    }
  }
  & .card {
    &.bs-active {
      & .accordion__block__btn {
        background-color: #0d9278;
        &::after {
          transform: rotate(-45deg);
        }
      }
    }
  }

  & .accordion__block {
    &__btn {
      position: relative;
      width: 100%;
      padding: 15px 20px;
      border: none;
      text-align: left;
      font-size: 20px;
      cursor: pointer;
      background-color: #1abc9c;
      color: #fff;
      &::after {
        content: "";
        position: absolute;
        right: 22px;
        top: 0;
        bottom: 0;
        width: 15px;
        height: 15px;
        margin-top: auto;
        margin-bottom: auto;
        border-top: 2px solid #fff;
        border-right: 2px solid #fff;
        transform: rotate(135deg);
        transition: all 0.4s ease-in-out;
      }
      span,h1,h2,h3,h4,h5,h6,p {
        pointer-events: none;
      }
    }
  }
  & .card-header {
    &__icon {
      display: inline-block;
      width: 50px;
      height: 50px;
      margin-right: 15px;
      object-fit: scale-down;
    }
  }
  &__floating-panel-container {
    position:relative;
  }
  &__floating-panel {
    display: none;
    padding: 0 15px;
    &.bs-visibility-hidden {
      position:absolute;
      visibility: hidden;
    }

    &.show {
      display: block;
    }
    &:not(.show) {
      display: none;
    }
  }
  &__navigation-panel-container {
    height: 0;
  }
  .collapse:not(.show) {
    display: none;
  }
  .collapsing {
    position: relative;
    height: 0;
    overflow: hidden;
    transition: height 0.35s ease;
  }
}
