@import "../../../theme-defaults.scss";

.accordion__block {
  &__btn {
    font-size: 20px;
    border: none;
    cursor: pointer;
    background-color: transparent;
    padding: 15px 20px;
    background-color: #1abc9c;
    width: 100%;
    text-align: left;
    color: #fff;
    position: relative;
    &:not(.expand-fixed)::after {
      content: "";
      width: 15px;
      height: 15px;
      border-top: 2px solid #fff;
      border-right: 2px solid #fff;
      position: absolute;
      right: 22px;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      transform: rotate(135deg);
      transition: all 0.4s ease-in-out;
    }
    span,h1,h2,h3,h4,h5,h6,p {
      pointer-events: none;
    }
    &[aria-expanded="true"] {
      background-color: #0d9278;
      &::after {
        transform: rotate(-45deg);
      }
    }
  }
}

$bs-advance-accordion: ".bs-advance-accordion";

#{$bs-advance-accordion} {
  display: flex;

  &--content-right {
    flex-direction: row-reverse;
  }

  &__left-container,
  &__right-container {
    flex: 1;
    flex-basis: 50%;
    .card-header {
      &__icon {
        margin-right: 15px;
        width: 50px;
        height: 50px;
        object-fit: scale-down;
      }
    }
    .card-body {
      padding: 10px 0;
    }
  }
  &__left-content-panel {
    display: none;
    padding: 0 15px;
    &.show {
      display: block;
    }
  }
  &--single-col {
    #{$bs-advance-accordion}__left-container {
      display: none;
    }
    #{$bs-advance-accordion}__right-container {
      flex-basis: 100%;
      div[id^="accordion"] {
        display: block;
      }
    }
    #{$bs-advance-accordion}__left-content-panel {
      display: block;
    }
  }
  .collapse:not(.show):not(.expand-fixed) {
    display: none;
  }
  .collapsing:not(.expand-fixed) {
    position: relative;
    height: 0;
    overflow: hidden;
    transition: height 0.35s ease;
  }
}
