 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

 //Video Styles
$DEFAULT__bs-div-video-wrapper--height: 100%;
$DEFAULT__bs-div-video-wrapper--width: 100%;
$DEFAULT__bs-div-video--height: 100%;
$DEFAULT__bs-div-video--width: 100%;

//Background image styles
$DEFAULT__bs-div-image-wrapper--height: 100%;
$DEFAULT__bs-div-image-wrapper--width: 100%;
$DEFAULT__bs-div-image--height: 100%;
$DEFAULT__bs-div-image--width: 100%;

.editor-styles-wrapper {
	.block-editor-inner-blocks {
		.bs-div {
			$this: &;
			position: relative;
			&.wp-block {
				.bs-div__video-container,
				.bs-div__image-container {
					z-index: 0;
				}
			}
		}
	}
}
.bs-div {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top left;
  &__video {
    margin: auto;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    .video-poster,
    video {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .video-poster {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &__inner {
    position: relative;
    &:after {
      content: "";
      display: table;
      clear: both;
    }
  }
  &__image-container {
    position: absolute;
    height: $DEFAULT__bs-div-image-wrapper--height;
    width: $DEFAULT__bs-div-image-wrapper--width;
    z-index: -1;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    img {
      height: $DEFAULT__bs-div-image--height;
      width: $DEFAULT__bs-div-image--width;
      object-fit: cover;
    }
  }
  &__video-container {
	position: absolute;
	height: $DEFAULT__bs-div-video-wrapper--height;
	width: $DEFAULT__bs-div-video-wrapper--width;
	z-index: -1;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	video {
		height: $DEFAULT__bs-div-video--height;
		width: $DEFAULT__bs-div-video--width;
		object-fit: cover;
	}
}
  .bs-background {
	img {
	  width: 100%;
	  height: 100%;
	}
  }
  &.has-text-color {
    p,
    li,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    pre {
      color: inherit;
    }
    p a,
    li a,
    pre a {
      color: inherit;
      text-decoration: underline;
    }
    .block-editor-warning__message {
      color: #000;
    }
  }
}

@media (min-width: 1200px) {
  .bs-div {
    &.has-text-color {
      p a,
      li a,
      pre a {
        &:hover {
          opacity: 0.7;
        }
      }
    }
  }
}
